.PvC {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    /* height: 100vh; */
    width: 100vw;
    /* background-color: rgba(0, 0, 0, 0.555); */
    color: #fff;
    margin-top: 67px;
}

.PvC--container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.PvC--icons {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 375px;
}

.player--pics {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.cpu--pics {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.player--pics .rock,
.cpu--pics .rock {
    width: 129px;
    height: 126px;
    border-radius: 50%;
    background-color: #F2F2F2;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border: solid 15px #9D1634;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    display: flex;
    align-items: center;
    justify-content: center;
}

.player--pics .paper,
.cpu--pics .paper {
    width: 129px;
    height: 126px;
    border-radius: 50%;
    background-color: #F2F2F2;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border: solid 15px #4664F4;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    display: flex;
    align-items: center;
    justify-content: center;
}

.player--pics .scissors,
.cpu--pics .scissors {
    width: 129px;
    height: 126px;
    border-radius: 50%;
    background-color: #F2F2F2;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border: solid 15px #EB9F0E;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    display: flex;
    align-items: center;
    justify-content: center;
}

.player--pics .lizard,
.cpu--pics .lizard {
    width: 129px;
    height: 126px;
    border-radius: 50%;
    background-color: #F2F2F2;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border: solid 15px #834EE3;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    display: flex;
    align-items: center;
    justify-content: center;
}

.player--pics .spock,
.cpu--pics .spock {
    width: 129px;
    height: 126px;
    border-radius: 50%;
    background-color: #F2F2F2;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border: solid 15px #3FB7CD;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    display: flex;
    align-items: center;
    justify-content: center;
}


.player--pics h1,
.cpu--pics h1 {
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 1.875px;
    color: #ffffff;
    text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    margin-top: 17px;
    text-transform: uppercase;
}

.cpu--pics .empty--cpu--icon {
    background-color: rgba(0, 0, 0, 0.1);;
    border: none;
    border-radius: 50%;
    width: 129px;
    height: 126px;
}

@media screen and (min-width: 900px) {
    
    .player--pics {
        flex-direction: column-reverse;
    }

    .cpu--pics {
        flex-direction: column-reverse;
    }

    .player--pics .rock,
    .cpu--pics .rock {
        width: 293px;
        height: 300px;
        border: solid 35px #9D1634;
    }

    .player--pics .paper,
    .cpu--pics .paper {
        width: 293px;
        height: 300px;
        border: solid 35px #4664F4;
    }
    .player--pics .scissors,
    .cpu--pics .scissors {
        width: 293px;
        height: 300px;
        border: solid 35px #EB9F0E;
    }
    .player--pics .lizard,
    .cpu--pics .lizard {
        width: 293px;
        height: 300px;
        border: solid 35px #834EE3;
    }
    .player--pics .spock,
    .cpu--pics .spock {
        width: 293px;
        height: 300px;
        border: solid 35px #3FB7CD;
    }

    .player--pics button img {
        width: 100px;
    }

    .cpu--pics button img {
        width: 100px;
    }

    .player--pics h1,
    .cpu--pics h1 {
        font-size: 24px;
        line-height: 32px;
        letter-spacing: 3px;
        margin-top: 0;
        margin-bottom: 20px;
    }

    .PvC {
        margin-top: 0;
    }

    .player--pics button,
    .cpu--pics button {
        margin: 0 30px;
    }

    /* .PvC--icons {
        justify-content: space-between;
        width: 70vw;
    } */

    .cpu--pics .empty--cpu--icon {
        width: 293px;
        height: 300px;
    }
}